<template>
    <div id="cardviewer">

        <!--CardView :cardData="exampleCard"></CardView-->
        <h1 v-if="loading">Loading, please Wait...</h1>
        <CardView
            v-for="(card, index) in cardData"
            :key="index"
            :cardData="card"
        ></CardView>
    </div>
</template>


<script>
import CardView from "./CardView.vue";

export default {
    components: {
        CardView
    },
    inject: ['$pokemon'],
    props: [
        'searchData'

    ],
    data() {
        return {
            cardData: [],
            savedKey: '',
            loading: false,
            exampleCard: {
                "id": "swsh4-25",
                "name": "Charizard",
                "supertype": "Pokémon",
                "subtypes": [
                    "Stage 2",
                ],
                "hp": "170",
                "types": [
                    "Fire"
                ],
                "evolvesFrom": "Charmeleon",
                "abilities": [
                    {
                        "name": "Battle Sense",
                        "text": "Once during your turn, you may look at the top 3 cards of your deck and put 1 of them into your hand. Discard the other cards.",
                        "type": "Ability"
                    }
                ],
                "attacks": [
                    {
                        "name": "Royal Blaze",
                        "cost": [
                            "Fire",
                            "Fire"
                        ],
                        "convertedEnergyCost": 2,
                        "damage": "100+",
                        "text": "This attack does 50 more damage for each Leon card in your discard pile."
                    }
                ],
                "weaknesses": [
                    {
                        "type": "Water",
                        "value": "×2"
                    }
                ],
                "retreatCost": [
                    "Colorless",
                    "Colorless",
                    "Colorless"
                ],
                "convertedRetreatCost": 3,
                "set": {
                    "id": "swsh4",
                    "name": "Vivid Voltage",
                    "series": "Sword & Shield",
                    "printedTotal": 185,
                    "total": 203,
                    "legalities": {
                        "unlimited": "Legal",
                        "standard": "Legal",
                        "expanded": "Legal"
                    },
                    "ptcgoCode": "VIV",
                    "releaseDate": "2020/11/13",
                    "updatedAt": "2020/11/13 16:20:00",
                    "images": {
                        "symbol": "https://images.pokemontcg.io/swsh4/symbol.png",
                        "logo": "https://images.pokemontcg.io/swsh4/logo.png"
                    }
                },
                "number": "25",
                "artist": "Ryuta Fuse",
                "rarity": "Rare",
                "flavorText": "It spits fire that is hot enough to melt boulders. It may cause forest fires by blowing flames.",
                "nationalPokedexNumbers": [
                    6
                ],
                "legalities": {
                    "unlimited": "Legal",
                    "standard": "Legal",
                    "expanded": "Legal"
                },
                "images": {
                    "small": "https://images.pokemontcg.io/swsh4/25.png",
                    "large": "https://images.pokemontcg.io/swsh4/25_hires.png"
                },
                "tcgplayer": {
                    "url": "https://prices.pokemontcg.io/tcgplayer/swsh4-25",
                    "updatedAt": "2021/08/04",
                    "prices": {
                        "normal": {
                            "low": 1.73,
                            "mid": 3.54,
                            "high": 12.99,
                            "market": 2.82,
                            "directLow": 3.93
                        },
                        "reverseHolofoil": {
                            "low": 3,
                            "mid": 8.99,
                            "high": 100,
                            "market": 3.89,
                            "directLow": 4.46
                        }
                    }
                },
                "cardmarket": {
                    "url": "https://prices.pokemontcg.io/cardmarket/swsh4-25",
                    "updatedAt": "2021/08/04",
                    "prices": {
                        "averageSellPrice": 9.38,
                        "lowPrice": 8.95,
                        "trendPrice": 10.29,
                        "germanProLow": null,
                        "suggestedPrice": null,
                        "reverseHoloSell": null,
                        "reverseHoloLow": null,
                        "reverseHoloTrend": null,
                        "lowPriceExPlus": 8.95,
                        "avg1": 9.95,
                        "avg7": 9.35,
                        "avg30": 11.31,
                        "reverseHoloAvg1": null,
                        "reverseHoloAvg7": null,
                        "reverseHoloAvg30": null
                    }
                }
            }
        }
    }
    ,
    computed: {
    },
    watch: {
        searchData: function (newSearchData) {
            if (newSearchData !== null) {
                this.loading = true;
                let q = '';
                if (newSearchData.key !== null && this.savedKey == '') {
                    this.$pokemon.configure({
                        apiKey: newSearchData.key
                    })
                    this.savedKey = newSearchData.key
                    localStorage.setItem('PokeApiKey', JSON.stringify(this.savedKey))
                }
                if (newSearchData.pokemon != '') {
                    q = q.concat(`name:"${newSearchData.pokemon}*" `)
                }
                if (newSearchData.year != '') {
                    q = q.concat(`set.releaseDate:"*${newSearchData.year}*" `)
                }
                if (newSearchData.subtype != '') {
                    q = q.concat(`subtype:"${newSearchData.subtype}"`)
                }
                if (q != '') {
                    console.log("SEARCHED:", q);
                    if (this.savedKey != '') { console.log("Using API Key") }
                    this.$pokemon.card.all({ q }).then((cards) => {
                        this.loading = false
                        this.cardData = cards
                    })
                } else {
                    this.loading = false;
                }
            }
        }
    },
    methods: {
    },

}
</script>

<style scoped>
#cardviewer {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    row-gap: 2em;
    column-gap: .5em;
    margin-bottom: 1em;
}
</style>
